.Btn {
    background-color: deepskyblue;
    border: none;
    color: white;
    outline: none;
    border-radius: 12px;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    text-align: center;
}

.Answer{
    height: 90px;
    width: 150px;
    box-shadow: 0 2px 1px darkblue;
    font-size: 1.2rem;
    font-weight: bold;
}

.Big{

    height: 100px;
    width: 170px;
    box-shadow: 0 2px 3px darkblue;
    font-size: 1.6rem;
    font-weight: bold;
}

.Config{
    height:45px;
    width: 37px;
    font-size: 1.2rem;
    margin: 12px;
    font-weight: bold;
    box-shadow: 0 2px 3px darkblue;

}

.Btn:hover{
    background-color: #046cff;
    border: solid 3px white;
    box-shadow: 1px 1px gray;
}

p{
    margin-bottom: auto;
}

.Small{
    background-color: rgba(0,191,255,0.5);
    font-size: .75rem;
    height: 40px;
    width: 75px;
    margin-top: 10%;
    box-shadow: 0 1px 1px darkblue;
}